
.menu{position:absolute;top:1.5rem;right:1.5rem;width:40%;background:#FFF;z-index:102;border-radius:4px;box-shadow:0 4px 6px rgba(0,0,0,0.3);transform-origin:100% 0;transition:all 150ms ease;transform:scale(0)
}
.menu ul{list-style:none;display:-ms-flexbox;display:flex
}
.menu ul li{height:4.5rem;width:100%;color:#444;font-size:1.6rem;padding:.5rem 1rem;text-align:left;display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;-ms-flex-align:center;align-items:center;transition:all 200ms ease
}
.menu ul li svg{margin:0;margin-right:5px;width:2.4rem;height:2.4rem;-ms-flex-negative:1;flex-shrink:1
}
.menu ul li p{font-size:1.4rem;color:#444
}
.menu ul li:active{background:rgba(0,0,0,0.1)
}
.menuOpen{transform:scale(1)
}
.schedule{margin-top:5px;width:100%;padding:4px;display:-ms-flexbox;display:flex
}
.schedule .col{width:100%;margin:1px;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column
}
.schedule .col .class{width:100%;height:46px;margin:1px;padding:5px;text-align:center;background:#FFF;border-radius:2px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;word-break:break-all;transition:all 200ms;overflow:hidden
}
.schedule .col .class p{width:100%;display:block
}
.schedule .col .head{height:32px;opacity:.5
}
.schedule .col .title{font-weight:bold;opacity:.5
}
.schedule .col .normal{font-size:12px;line-height:16px;color:#FFF;background:#5e8ed6
}
.schedule .col .empty{background:rgba(255,255,255,0.5)
}
.schedule .col .type10,.schedule .col .type12,.schedule .col .type20,.schedule .col .type30,.schedule .col .type40,.schedule .col .type47,.schedule .col .type50,.schedule .col .type60{height:94px
}
.schedule .col .type11,.schedule .col .type15,.schedule .col .type31,.schedule .col .type35,.schedule .col .type53,.schedule .col .type56{height:142px
}
.schedule .col .type21,.schedule .col .type25,.schedule .col .type41,.schedule .col .type44{height:190px
}
.schedule .col1{width:70%
}
.maskTransparent{position:fixed;width:100%;height:100%;background:transparent;z-index:999;opacity:0;transition:all 300ms ease
}
.showMask{opacity:1;pointer-events:auto
}


